import React, { Component } from 'react';

class EditUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: this.props.userEditObj.id,
            name: this.props.userEditObj.name,
            tel: this.props.userEditObj.tel,
            permission: this.props.userEditObj.permission,
        }
    }
   
    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]:value
        });
    }
    saveEditBtn = () => {
        var info = {};
        info.id = this.state.id;
        info.name = this.state.name;
        info.tel = this.state.tel;
        info.permission = this.state.permission;
        this.props.getUserInfo(info);
        this.props.changeEditUserStatus();
    }
    render() {  
        return (
            <div className="col-sm-12 p-0">
            <div className="card mb-4">
            <div className="card-header text-white bg-warning">
               Thay đổi thông tin User
            </div>
            <form>
           
            <div className="card-content p-3">
                <div className="form-group">
                <input type="text" defaultValue={this.props.userEditObj.name} className="form-control" name="name" id="user_name" placeholder="Tên User" onChange={(event) => this.isChange(event)} />
                </div>
                <div className="form-group">
                <input type="text" defaultValue={this.props.userEditObj.tel} className="form-control" name="tel" id="user_phone" placeholder="Điện thoại" onChange={(event) => this.isChange(event)}/>
                </div>
                <div className="form-group">
                <select className="custom-select" name="permission" defaultValue={this.props.userEditObj.permission} onChange={(event) => this.isChange(event)}>
                    <option value={1}>Admin</option>
                    <option value={2}>Moderator</option>
                    <option value={3}>Normarl User</option>
                </select>
                </div>
                <input type="button" className="btn btn-danger btn-block text-center m-auto" value="Lưu" onClick={() => this.saveEditBtn()}/>
            </div>
            </form>
        </div>
        </div>
        );
    }
}

export default EditUser;