import React, { Component } from 'react';
import EditUser from './editUser';

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tempValue: '',
      userObj: {}
    }
  }
  
    isChange = (event) => {
        this.setState({
          tempValue:event.target.value
        });
        this.props.seacrhText(this.state.tempValue);
    } 

    getUserInfo = (info) => {
      this.setState({
        userObj: info
      });
     this.props.getUserEditInfoApp(info);
    }

    showEditUser = () => {
        if(this.props.editUserStatus === true){
           
          return <EditUser getUserInfo={(info) => this.getUserInfo(info)} userEditObj={this.props.userEditObj} changeEditUserStatus={() => this.props.changeEditUserStatus()}></EditUser>
        }
    }

    hienThiNut = () => {
        if(this.props.hienThiForm === true){
            return (
                <button type="button" className="btn btn-outline-secondary btn-block text-center m-auto" onClick={() => this.props.ketnoi()}>Đóng</button>
            )
        }
        else {
            return (
                <button type="button" className="btn btn-outline-primary btn-block text-center" onClick={() => this.props.ketnoi()}>Thêm</button>
            )
        }
    }
    render() { 
        return (
            <div className="col-sm-12">
             {this.showEditUser()}
            <div className="row">
              <div className="input-group mb-3 col-sm-6">
                <input type="text" className="form-control" placeholder="Nhập tên để tìm kiếm" aria-describedby="basic-addon2" onChange={(event) => this.isChange(event)} />
                <div className="input-group-append">
                 <span style={{cursor:'pointer'}} className="input-group-text btn-info" id="basic-addon2" onClick={(dl) => this.props.seacrhText(this.state.tempValue)}>Tìm kiếm</span>
                </div>
              </div>
              <div className="input-group mb-3 col-sm-6">
                {this.hienThiNut()}
            </div>
           

            </div>
          </div>

         
        
        );
    }
}

export default Search;