import React, { Component } from 'react';
import Header from './Component/header';
import './App.css';
import Search from './Component/search';
import TableData from './Component/tableData';
import AddUser from './Component/addUser';
import DataUser from './Component/Data';
import { v4 as uuidv4 } from 'uuid';
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hienThiForm: true,
      data: DataUser,
      searchText:'',
      editUserStatus: false,
      userEditObject: {}
    }
  }

  getTextSearch = (dl) => {
    this.setState({
      searchText: dl
    });
  }

  getNewUser = (name, tel, Permission) => {
   
    var item = {};
    item.id = uuidv4();
    item.name = name;
    item.tel = tel;
    item.permission = Permission;
    var items = this.state.data;
    items.push(item);
    console.log(items);
    this.setState({
      data: items
    });
  }

  doiTrangThai = () => {
    this.setState({
      hienThiForm: !this.state.hienThiForm
    });
  }

  editUser = (user) => {
    this.setState({
      userEditObject: user
    });
  }

  changeEditUserStatus = () => {
    this.setState({
      editUserStatus: !this.state.editUserStatus
    });
  }

  getUserEditInfoApp = (info) => {
    this.state.data.forEach((value,key) => {
      if(value.id === info.id){
        value.name = info.name;
        value.tel = info.tel;
        value.permission = info.permission;
      }
    })
  }

  deleteUser = (idUser) => {
    var dataAfterDelete = this.state.data.filter(item => item.id !== idUser);
    this.setState({
      data: dataAfterDelete
    });
  }
  render() { 
    var result = [];
    this.state.data.forEach((item) => {
      if(item.name.indexOf(this.state.searchText) !== -1){
        result.push(item);
      }
    })
   
    return (
      <div className="App">
          <Header/>
          <div className="container" style={{marginTop: '30px'}}>
            <div className="row">
              <Search getUserEditInfoApp={(info) => this.getUserEditInfoApp(info)  } userEditObj={this.state.userEditObject} changeEditUserStatus={() => this.changeEditUserStatus()} editUserStatus={this.state.editUserStatus} ketnoi={() => this.doiTrangThai()} hienThiForm={this.state.hienThiForm} seacrhText={(dl) => this.getTextSearch(dl)}></Search>
              <TableData deleteUser={(idUser) => this.deleteUser(idUser)} changeEditUserStatus={() => this.changeEditUserStatus()} editFunc={(user) => this.editUser(user)} dataUserProps={result}></TableData>
              <AddUser hienThiForm={this.state.hienThiForm} add={(name, tel, Permission) => this.getNewUser(name, tel, Permission)}></AddUser>
            </div>
          </div>
      </div>
    );
  }
}
export default App;