import React, { Component } from 'react';
import TableDataRow from './tableDataRow';

class TableData extends Component {
  deleteBtnClick = (idUser) => {
    this.props.deleteUser(idUser);
  }
  mappingDataUser = () => this.props.dataUserProps.map((value,key) =>(
     <TableDataRow deleteIdUser={(idUser) => this.deleteBtnClick(idUser)} changeEditUserStatus={() => this.props.changeEditUserStatus()} editFuncClick={(user) => this.props.editFunc(value)} id={value.id} key={key} stt={key+1} nameUser={value.name} phoneUser={value.tel} permissionUser={value.permission}></TableDataRow>
    ))
    render() {
      
        return (
            <div className="col">
            <table className="table table-striped">
              <thead>
                <tr>
                  <th>STT</th>
                  <th>Tên</th>
                  <th>Điện thoại</th>
                  <th>Quyền</th>
                  <th>Thao tác</th>
                </tr>
              </thead>
              <tbody>
             {this.mappingDataUser()}
            
              </tbody>
            </table>
          </div>
        );
    }
}

export default TableData;