import React, { Component } from 'react';

class TableDataRow extends Component {
    permissionUserShow = () => {
        if(this.props.permissionUser === "1"){
            return "Admin";
        }
        else if(this.props.permissionUser === "2"){ return "Moderator";}
        else { return "Normal User"};
    }
    //this.props.editFuncClick();
    editClick = () =>{
        this.props.editFuncClick();
        this.props.changeEditUserStatus();
    }
    deleteBtnUser = (idUser) => {
        this.props.deleteIdUser(idUser);
    }
    render() {
        return (
        <tr>
            <td>{this.props.stt}</td>
            <td>{this.props.nameUser}</td>
            <td>{this.props.phoneUser}</td>
            <td>
                {this.permissionUserShow()}
            </td>
            <td>
              <button type="button" className="btn btn-info" onClick={() => this.editClick()}>Sửa</button>
              <button type="button" className="btn btn-danger" onClick={(idUser) => this.deleteBtnUser(this.props.id)}>Xóa</button>
            </td>
          </tr>
        );
    }
}

export default TableDataRow;