import React, { Component } from 'react';

class AddUser extends Component {
    constructor(props) {
        super(props);
        this.state= {
            id: '',
            name:'',
            tel: '',
            Permission: '',

        }
    }
    
    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]:value
        });

        var item = {};
        item.id = this.state.id;
        item.name = this.state.name;
        item.tel = this.state.tel;
        item.permission = this.state.Permission;  
    }
   kiemTraTrangThai = () => {
       if(this.props.hienThiForm === true){
           return (
            <div className="col">
            <div className="card" style={{width: '18rem'}}>
            <div className="card-header">
                Thêm mới user vào hệ thống
            </div>
            <form>
            <div className="card-content p-3">
                <div className="form-group">
                <input type="text" className="form-control" name="name" id="user_name" placeholder="Tên User" onChange={(event) => this.isChange(event)}/>
                </div>
                <div className="form-group">
                <input type="text" className="form-control" name="tel" id="user_phone" placeholder="Điện thoại" onChange={(event) => this.isChange(event)}/>
                </div>
                <div className="form-group">
                <select className="custom-select" name="Permission"  onChange={(event) => this.isChange(event)}>
                    <option value={1}>Admin</option>
                    <option value={2}>Moderator</option>
                    <option value={3}>Normarl User</option>
                </select>
                </div>
                <input type="reset" className="btn btn-primary btn-block text-center m-auto" onClick={(name, tel, permission) => this.props.add(this.state.name, this.state.tel, this.state.Permission)} value="Thêm mới" />
            </div>
            </form>
        </div>
        </div>
           )
       }
   }
    render() {
        
        
        return (
           <div>
                {this.kiemTraTrangThai()}
           </div>
               
        
        )
    }
}

export default AddUser;